@import "./contact.scss";

$desktop: 1024px;

@media (max-width: $desktop) {
    .contact-container {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 100vw;
        height: 100vh;
        min-height: 100%;
        justify-content: center;
        left: 0;
        background-color: $unselected-menu;
    }
    
    .contact-container h1 {
        font-family: 'Poppins', sans-serif;
        font-weight: lighter;
        text-align: center;
        color: $white-text;
    }
    
    .mail {
        position: inherit;
        display: flex; 
        flex-direction: row;
        background-color: $unselected-menu; 
        padding: 2%;
        justify-content: center;
        justify-self: center;
        width: 100vw;
    }
    
    .mail h3 {
        font-family: 'Raleway';
        font-size: 2.5vw;
    }
    
    .mail a {
        color: $white-text;
        transition: 0.4s;
    }
    
    .mail:hover .fa-mail-bulk {
        color: #0072c6;
        transition: 0.7s;
    }
    
    .mail a:hover {
        color: $primary;
        transition: 0.7s;
    }
    
    .social {
        position: inherit;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        background-color: $unselected-menu;
        padding: 2%;
        width: 100vw;
    }
    
    .social h4 {
        font-family: 'Raleway';
        font-weight: 600;
        display: inline-block;
        vertical-align: middle;
        color: $primary;
    }
    
    .linkedin {
        position: relative;
        padding: 2%;
    }
    
    .linkedin a{
        color: initial;
    }
    
    .twitter {
        position: relative;
        padding: 2%;
    }
    
    .twitter a {
        color: initial;
    }
    
    .fa-mail-bulk {
        font-size: 3vw;
        transition: 0.4s;
        color: initial;
    }
    
    .fa-linkedin {
        font-size: 3vw;
        transition: 0.5s;
    }
    
    .fa-linkedin:hover {
        color: $white-text;
        transition: 0.5s;
    }
    
    .fa-twitter {
        transition: 0.5s;
        font-size: 3vw;
    }
    
    .fa-twitter:hover {
        color: #1da1f2;
        transition: 0.5s;
    }

    .parallax-inner {
        transform: translate3d(0%, 10%, 0px) !important;
    }
}    