@import "./about-me.scss";

$desktop: 1024px;

@media (max-width: $desktop) {
    .about-me-container {
        position: relative;
        display: flex;
        flex-direction: column;
        width: 100vw;
        height: 100vh;
        min-height: 100%;
        justify-content: center;
        left: 0;
    }
    
    .about-me-container h1{
        text-align: center;
    }
    
    .few-words {
       position: relative;
    }
    
    .few-words p{
        font-family: 'Raleway';
        font-size: 2.5vw;
        text-align: center;
    }

    //FAVORITE LANGUAGES DIV
    .favorite-languages {
        position: relative;
        width: 100vw;
        background-color: $unselected-menu;
        
        text-align: center;
        display: flex;
        flex-direction: column;
        padding: 2%;
    }
    
    .favorite-languages h1 {
        font-family: 'Poppins', sans-serif;
        font-weight: 500;
        font-size: 2.5vw;
        transition: 0.6s;
        user-select: none;
    }
    
    .favorite-languages h1:hover {
        color: $primary;
        transition: 0.6s;
    }
    
    .favorite-languages:hover .fa-hashtag {
        color: #783bd2;
        transition: 0.6s;
    }
    
    .favorite-languages:hover .fa-python {
        color: #ffde57;
        transition: 0.6s;
    }
    
    .favorite-languages:hover .fa-react {
        color: #00d8ff;
        transition: 0.6s;
    }
    
    .fa-cuttlefish
    {
        transition: 0.6s;
        font-size: 2vw;
    }

    .fa-hashtag
    {
        transition: 0.6s;
        font-size: 2vw;
    }
    
    .fa-python {
        font-size: 2.5vw;
        transition: 0.6s;
    }
    
    .favorite-languages .fa-react {
        font-size: 2.5vw;
        transition: 0.6s;
    }
    
    //FAVORITE TOOLS DIV
    .favorite-tools {

        width: 85%;
        background-color: $unselected-menu;
        align-self: flex-end;
        text-align: center;
        padding: 2%;
        justify-self: center;
    }
    
    .favorite-tools h1 {
        font-family: 'Poppins', sans-serif;
        font-weight: 500;
        font-size: 2vw;
        color: $white-text;
        transition: 0.6s;
        user-select: none;
        text-align: center;
    }
    
    .favorite-tools h4 {
        font-family: 'Raleway';
        font-weight: bold;
        font-size: 2.5vw;
        color: $primary;
        justify-self: end;
        margin-bottom: 0;
    }
    
    .favorite-tools img {
        vertical-align: middle;
    }
    
    .favorite-tools:hover h1{
        color: $primary;
        transition: 0.6s;
    }
}    